
.fade-enter-active,
.fade-leave-active {
  transition: opacity .2s;
}
.overlay-enter-active,
.overlay-leave-active {
  transition: opacity .4s;
}
.fade-enter,
.fade-leave-to,
.overlay-enter,
.overlay-leave-to {
  opacity: 0;
}
.form-required {
  color: red;
}



